import React from 'react'
const Title = ({title}) => <h1>{title}</h1>
const Engine = ({engine}) => <li>{engine}</li>
const Gearbox = ({gearbox}) => <li>{gearbox}</li>
const Type = ({type}) => <li>{type}</li>
const SpearWheel = ({spearWheel}) => <li>{spearWheel}</li>
const SeatingCapacity = ({seatingCapacity}) => <li>{seatingCapacity}</li>
const Bhp = ({bhp}) => <li>{bhp}</li>
const Torque = ({torque}) => <li>{torque}</li>

const Specification = ({subject,engine,gearbox,type,spearWheel,seatingCapacity,bhp,torque}) => (
    <div>
        <Title title ={`This is ${subject}`}/>
        <Engine engine = {`engine is ${engine}`}/>
        <Gearbox gearbox = {`gearbox type :${gearbox}`}/>
        <Type type = {`type of vehicle : ${type}`}/>
        <Bhp bhp = {`bhp :${bhp}`}/>
        <SpearWheel spearWheel = {`spearwheel :${spearWheel}`}/>
        <SeatingCapacity seatingCapacity = {`seatingcapacity :${seatingCapacity}`}/>
        <Torque torque = {`torque :${torque}`}/>

    </div>
);

export default function Modular() {
    const car = {
        subject:"car specification",
        engine:"mahindra 2.64L",
        gearbox:"6 speed manual",
        type:"suv",
        spareWheel:"yes 1",
        seatingCapacity:"7",
        bhp:"100",
        torque:"120NM"
    }
    return (
        <div>
            <Specification {...car}/>
        </div>
    )
}